import 'rxjs/Rx';

import * as moment from 'moment';

import {
  AlertController,
  App,
  LoadingController,
  MenuController,
  NavController,
  NavParams,
  SegmentButton
} from 'ionic-angular';
import { SiteVisit, WorkerModel } from '../../classes/worker.model';

import { AuthService } from '../../providers/auth';
import { CheckInOutService } from '../../providers/check-in';
import { Component } from '@angular/core';
import { DEFAULT_PERSON } from '../../providers/worker';
import { FollowersPage } from '../followers/followers';
import { Geolocation } from '@ionic-native/geolocation';
import { PrestartConfiguration } from '../../classes/prestart-configuration.model';
import { PrestartConfigurationService } from '../../providers/prestart-configuration';
import { ProfileModel } from './worker-clockon.model';
import { SettingsPage } from '../settings/settings';
import { Site } from '../../classes/site.model';
import { SiteService } from '../../providers/sites';
import { SwmPage } from '../swm/swm';
import { SwmService } from '../../providers/swm';
import { SwmsModel } from '../../classes/swm.model';
import { TaskService } from '../../providers/task';
import { TasksModel } from '../../classes/task.model';
import { ToastController } from 'ionic-angular/components/toast/toast-controller';

@Component({
  selector: 'worker-clockon-page',
  templateUrl: 'worker-clockon.html'
})
export class WorkerClockonPage {
  defaultPersonImage = DEFAULT_PERSON;

  display: string;
  profile: ProfileModel = new ProfileModel();
  loading: boolean = false;
  siteTaskList: TasksModel = new TasksModel();
  allTasks: TasksModel = new TasksModel();
  worker: WorkerModel;
  swmlist: SwmsModel = new SwmsModel();
  activeSWMs: SwmsModel = new SwmsModel(); // these are determined from the tasks
  prestartConfiguration: PrestartConfiguration = null;

  categories_checkbox_open: boolean;
  siteId: number = 0;

  clockedOn = false;
  clockedOff = false;
  public signingOff = false;

  requiredSWMids: number[] = [];
  lunch = false;
  public location: {
    lat: number
    lon: number
  } = {
      lat: null,
      lon: null
    };


  constructor(
    public menu: MenuController,
    public app: App,
    public navParams: NavParams,
    public taskService: TaskService,
    public loadingCtrl: LoadingController,
    public checkOutService: CheckInOutService,
    private geolocation: Geolocation,
    private alertCtrl: AlertController,
    private nav: NavController,
    private swmService: SwmService,
    private toast: ToastController,
    private siteService: SiteService,
    private auth: AuthService,
    private prestartConfigurationService: PrestartConfigurationService,
  ) {
    this.display = "list";

    this.worker = this.navParams.get('worker');
    console.log('Worker is', this.worker);
  }

  ionViewDidLoad() {
    this.profile.user = this.auth.user;
    this.profile.following = [];
    this.profile.followers = [];
    this.profile.posts = [];

    this.loading = true;
    const loader = this.loadingCtrl.create();
    loader.present();

    const op1 = this.swmService
      .getData()
      .then(data => {
        console.log("got swms data in prestart");
        this.swmlist.items = data.items;
      });

    const op2 = this.taskService
      .getData()
      .then(data => {
        console.log("got tasks data in worker-clockon");
        this.allTasks.items = data.items;
        this.updateSiteTasksList();
        this.generateSwms();
      });

    const op3 = this.updateSitesList();

    const loaded = () => { loader.dismiss(); this.loading = false; };
    Promise.all([op1, op2, op3]).then(loaded, loaded);

    this.getGeoLocation();
  }

  refreshSites() {
    this.loading = true;
    const loader = this.loadingCtrl.create();
    loader.present();
    const done = () => {
      loader.dismiss();
      this.loading = false;
    }
    this.updateSitesList().then(done, done);
  }

  goToFollowersList() {
    // close the menu when clicking a link from the menu
    this.menu.close();
    this.app.getRootNav().push(FollowersPage, {
      list: this.profile.followers
    });
  }

  goToFollowingList() {
    // close the menu when clicking a link from the menu
    this.menu.close();
    this.app.getRootNav().push(FollowersPage, {
      list: this.profile.following
    });
  }

  goToSettings() {
    // close the menu when clicking a link from the menu
    this.menu.close();
    this.app.getRootNav().push(SettingsPage);
  }

  onSegmentChanged(segmentButton: SegmentButton) {
    // console.log('Segment changed to', segmentButton.value);
  }

  onSegmentSelected(segmentButton: SegmentButton) {
    // console.log('Segment selected', segmentButton.value);
  }

  chooseCategory() {
    console.log("chooseCategory")
    let alert = this.alertCtrl.create({
      cssClass: 'category-prompt'
    });
    alert.setTitle('Site');
    // Site is allowed if it is allowed list or as a backup
    const isSiteAllowedForWorker =
      (site: Site) => this.worker.sitesAllowed.indexOf(site.id) >= 0 ||
        this.prestartConfigurationService.prestarts.find((p) => p.siteId === site.id && p.backupStaff.indexOf(this.worker.id) >= 0);
    this.siteService.sites
      .filter((site) => isSiteAllowedForWorker)
      .forEach(({ label, id, open }, index) => {
        alert.addInput({
          type: 'radio',
          label: label + (open ? '' : ' [NOT OPENED]'),
          value: '' + id,
          checked: id === this.siteId
        });
      });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Confirm',
      handler: data => {
        console.log('Checkbox data:', data);
        this.categories_checkbox_open = false;
        this.selectSite(+data);
      }
    });
    alert.present().then(() => {
      this.categories_checkbox_open = true;
    });
  }

  // Update list of tasks based on selected site
  updateSiteTasksList() {
    if (!this.siteId) {
      this.siteTaskList.items = [];
      return;
    }
    this.siteTaskList.items = this.allTasks.items
      .filter((task) => this.prestartConfiguration.tasks.indexOf(task.id) >= 0);
  }

  getSiteName() {
    if (!this.siteId) {
      return '<Please select a site>';
    } else {
      return (this.siteService.byId(this.siteId) || { label: 'Unknown' }).label;
    }
  }

  getSite() {
    return this.siteService.byId(this.siteId);
  }

  // Generate the active swms from the active tasks
  // Save the updated swm list to the swmService for use elsewhere
  generateSwms() {
    console.log("generate swms");
    // clear the list first-child
    this.requiredSWMids = [];
    // build an array of all the swm ids required
    this.siteTaskList.items.forEach((task) => {
      // only if the task is active
      if (task.selected) {
        task.swms.forEach((id) => {
          console.log("swm id " + id)
          if (this.requiredSWMids.indexOf(id) < 0) {
            this.requiredSWMids.push(id)
          }
        })
      }
    })
    console.log(this.requiredSWMids);
    // save requiredSWMids to swmService to use later to cross reference
    // against each worker to make sure they have done the swm courses
    // required for the day's tasks
    this.swmService.setRequiredSwms(this.requiredSWMids);

    // generate a local list of just the active swms to display
    this.activeSWMs.items = this.swmlist.items.filter((swm) => {
      console.log("swm " + swm.name + " is " + swm.inuse)
      swm.inuse = false;
      // now see if swm id in the active lists
      if (this.requiredSWMids.indexOf(swm.id) >= 0) {
        swm.inuse = true;
      }
      return swm.inuse;
    })
    // update the service
    this.swmService.updateSwmList(this.swmlist);
  }

  startSignOff() {
    this.signingOff = true;
    console.log("start the signoff process for " + this.worker.name);
    this.getGeoLocation(); // get fresh location
    if (!this.location) {
      this.location = { lat: null, lon: null };
    }
    this.updateWorkerClockOff();
    this.checkOutService
      .checkOut(this.siteId, this.lunch, this.location.lat, this.location.lon, this.worker.id)
      .subscribe((response) => {
        this.signingOff = false;
        if (!response.success) {
          this.notify(response.message);
        }
      }, (error) => {
        this.signingOff = false;
        this.notify('Server error');
        console.error(error);
      });
  }

  startSignIn() {
    console.log("start the signin process");
    this.generateSwms();
    this.nav.push(SwmPage, {
      'worker': this.worker,
      siteId: this.siteId,
      'prestart': this.prestartConfiguration
    });
  }

  public getAllowedSites(): Site[] {
    // First, sites, where he checked-in
    const siteFilter = (site: Site) =>
      this.worker.siteVisits.find((i) =>
        i.id === site.id && i.checkInDate && !i.checkOutDate);
    const checkInSites = this.siteService.sites.filter(siteFilter);
    if (checkInSites.length) {
      return checkInSites;
    }
    // Then, all other sites
    const openedSites = this.siteService.sites.filter((site: Site) => site.open);
    return openedSites;
  }

  private notify(text) {
    const toast = this.toast.create({
      message: text,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  // Worker has separate clock on and clock off times for each site
  private updateClokOnOffBySiteVisits(visits: SiteVisit[]) {
    if (!visits) {
      return;
    }
    const siteVisits = visits.filter((i) => i.id === this.siteId);
    this.worker.clockedOff = false;
    this.worker.clockedOn = false;
    this.worker.clockOff = '';
    this.worker.clockOn = '';
    if (siteVisits.length) {
      const siteVisit = siteVisits[siteVisits.length - 1];
      this.worker.clockedOn = !!siteVisit.checkInDate;
      if (this.worker.clockedOn) {
        this.worker.clockOn = moment(siteVisit.checkInDate).format('HH:mm:ss')
      }
      this.worker.clockedOff = !!siteVisit.checkOutDate;
      if (this.worker.clockedOff) {
        this.worker.clockOff = moment(siteVisit.checkOutDate).format('HH:mm:ss')
      }
    }
  }

  private updateWorkerClockOff() {
    this.worker.clockedOff = true;
    this.worker.clockOff = new Date().toTimeString().split(' ')[0];
    this.worker.siteVisits = this.worker.siteVisits || [];
    let visits = this.worker.siteVisits.filter((i) => i.id === this.siteId);
    const visit = visits[visits.length - 1];
    visit.checkOutDate = new Date();
  }

  private selectSite(id: number) {
    this.siteId = id;
    const siteIsOpen = this.getSite().open;
    if (siteIsOpen) {
      this.updatePrestartConfiguration()
        .then(() => this.updateSiteTasksList())
        .then(() => this.updateClokOnOffBySiteVisits(this.worker.siteVisits));
    }
  }

  // If we have only one site, it should be default
  private selectDefaultSite() {
    const siteId = this.getDefaultSite();
    if (siteId) {
      this.selectSite(siteId);
    }
  }

  private getDefaultSite(): number {
    const allowedSites = this.getAllowedSites();
    return allowedSites.length === 1 ? allowedSites[0].id : 0;
  }

  private updateSitesList() {
    return this.siteService
      .getData()
      .then(() => this.selectDefaultSite());
  }

  private updatePrestartConfiguration() {
    return this.prestartConfigurationService
      .getData()
      .then(() => {
        this.prestartConfiguration = this.prestartConfigurationService.getBySiteId(this.siteId);
        console.log(this.prestartConfiguration);
      })
  }

  private getGeoLocation() {
    this.geolocation.getCurrentPosition().then((resp) => {
      console.log("Location: lat = " + resp.coords.latitude + " long " + resp.coords.longitude);
      this.location.lat = resp.coords.latitude;
      this.location.lon = resp.coords.longitude;
    }).catch((error) => {
      console.log('Error getting location', error);
    });
  }
}



