import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';

import { Site } from '../../classes/site.model';
import { SiteService } from '../../providers/sites';
import { Slides } from 'ionic-angular';

@Component({
  selector: 'site-select',
  templateUrl: 'site-select.component.html'
})

export class SiteSelectComponent implements OnInit, OnChanges, AfterViewInit {

  @Input() public loaded: boolean = true;
  @Input() public selected: number;
  @Input() public allowed: number[] = [];
  @Output() public onSelected = new EventEmitter<Site>();
  @ViewChild(Slides) slides: Slides;

  public sitesList: Site[] = [];

  constructor(private sites: SiteService) { }

  ngOnInit() {
    if (!this.sites.sites.length) {
      this.updateSitesList();
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['allowed']) {
      this.updateSitesList();
      this.slides && this.slides.update();
    }
    if (changes['selected'] && changes['selected'].currentValue) {
      this.selectSlide(this.selected);
    }
  }

  ngAfterViewInit(): void {
    if (this.selected) {
      this.selectSlide(this.selected);
    }
  }

  public slideChanged() {
    let currentIndex = this.slides.realIndex;
    console.log('Selected index', currentIndex, JSON.stringify(this.allowed));
    const siteId = this.allowed[currentIndex];
    this.selected = siteId;
    const site = this.sites.sites.find((s) => s.id === siteId);
    console.log('Selected site', siteId, site);
    this.onSelected.emit(site);
  }

  public siteIdentity(site: Site) {
    return site.id;
  }

  private updateSitesList() {
    this.sitesList = this.allowed.map((id) => {
      const site = this.sites.sites.find((s) => s.id === id);
      if (site) {
        return site;
      } else {
        console.error('Site not returned by sites service ' + id);
        return new Site(id, 'Unknown', false, null, '', '');
      }
    });
    if (!this.selected && this.sitesList.length) {
      this.selected = this.allowed[0];
      this.onSelected.emit(this.sitesList[0]);
    }
    console.log('Sites', this.sitesList);
  }

  private selectSlide(siteId: number) {
    const index = this.allowed.indexOf(siteId);
    if (index < 0) {
      console.error(`Request site ${siteId} is not in the list`, this.allowed);
      return;
    }
    this.slides && this.slides.slideTo(index);
  }
}



