import {
  AlertController,
  LoadingController,
  NavController,
  NavParams,
  ToastController
} from 'ionic-angular';

import { Component } from '@angular/core';
import { PrestartConfiguration } from '../../classes/prestart-configuration.model';
import { SafetyReminderPage } from '../safety-reminder/safety-reminder';
import { SwmDetailPage } from '../swm-detail/swm-detail';
import { SwmService } from '../../providers/swm';
import { SwmsModel } from '../../classes/swm.model';
import { WorkerModel } from '../../classes/worker.model';
import { WorkerService } from '../../providers/worker';
import { SharedService } from '../../providers/shared';

@Component({
  selector: 'page-swm',
  templateUrl: 'swm.html'
})
export class SwmPage {
  worker: WorkerModel;
  prestartConfiguration: PrestartConfiguration;
  siteId: number;
  loading: any;
  userHelperMode: boolean;
  activeSWMs: SwmsModel = new SwmsModel(); // these are determined from the tasks

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public swmService: SwmService,
    public loadingCtrl: LoadingController,
    private workerService: WorkerService,
    private alertCtrl: AlertController,
    private toast: ToastController,
    public sharedService: SharedService,
  ) {
    this.loading = this.loadingCtrl.create();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SwmPage');
    this.worker = this.navParams.get('worker');
    this.prestartConfiguration = this.navParams.get('prestart');
    this.siteId = +this.navParams.get('siteId');
    console.log('Worker', this.worker);
    this.updateActiveSwms();
    console.log("got active swm list")
    console.log(this.activeSWMs.items)
    this.userHelperMode = this.sharedService.userHelperMode;
  }

  smwOverride(swm) {
    this.presentConfirm(swm);
  }

  presentConfirm(swm) {
    let alert = this.alertCtrl.create({
      title: 'SWM Status',
      message: 'Have you completed this swm?',
      buttons: [
        {
          text: 'No',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Yes',
          handler: () => {
            console.log('SWM clicked');
            this.workerService
              .approveSwm(swm.id, this.siteId, this.worker.id)
              .then(() => {
                this.worker.claimedSwms.push({ swm: swm.id, site: this.siteId });
                this.updateActiveSwms();
              }, () => {
                this.notify('Server error. Please try again later');
                // TODO: Remove mock below after backend completion
                this.worker.claimedSwms.push({ swm: swm.id, site: this.siteId });
                this.updateActiveSwms();
              });
          }
        }
      ]
    });
    alert.present();
  }

  swmdetails(swmId, swm) {
    console.log("goto swm details for " + swmId);
    this.navCtrl.push(SwmDetailPage, { id: swmId, swm: swm });
  }

  gotoReminders() {
    console.log("gotoReminders")
    this.navCtrl.push(SafetyReminderPage, {
      'worker': this.worker,
      'siteId': this.siteId,
      'prestart': this.prestartConfiguration
    });
  }

  private notify(text, duration = 3000) {
    const toast = this.toast.create({
      message: text,
      duration,
      position: 'top'
    });

    toast.present();
  }

  private updateActiveSwms() {
    this.activeSWMs.items = this.swmService
      .getSwmList().items.filter((swm) => {
        // firstly, set the traffic light based on if worker has done swm courses
        if (!this.worker.hasValidSwm(this.siteId, swm.id)) {
          if (this.worker.hasClaimedSwm(this.siteId, swm.id)) {
            // Claimed for offline certification
            swm.red = false;
            swm.yellow = true;
            swm.green = false;
          } else {
            // SWM not performed
            swm.red = true;
            swm.yellow = false;
            swm.green = false;
          }
        } else {
          // Certification passed
          swm.red = false;
          swm.yellow = false;
          swm.green = true;
        }

        // add if in use
        return this.prestartConfiguration.swms.indexOf(swm.id) >= 0;
      });
    this.worker.swmCompliant =
      this.prestartConfiguration.swms.every((swmId) =>
        this.worker.hasValidSwm(this.siteId, swmId) ||
        this.worker.hasClaimedSwm(this.siteId, swmId)
      );
  }
}



