import { DEFAULT_PERSON, WorkerService } from '../../providers/worker';
import { LoadingController, NavController } from 'ionic-angular';

import { Component } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation';
import { PrestartConfiguration } from '../../classes/prestart-configuration.model';
import { PrestartConfigurationService } from '../../providers/prestart-configuration';
import { SharedService } from '../../providers/shared';
import { Subscription } from 'rxjs';
import { SwmPage } from '../swm/swm';
import { SwmService } from '../../providers/swm';
import { WorkersModel } from '../../classes/worker.model';

const TAB_UPDATE_INTERVAL = 60 * 1000;

/*
  Generated class for the ClockOn page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-clock-on',
  templateUrl: 'clock-on.html'
})
export class ClockOnPage {
  defaultPersonImage = DEFAULT_PERSON;

  prestart: PrestartConfiguration = null;
  workerlist: WorkersModel = new WorkersModel();
  workersNotStartedThisSite: WorkersModel = new WorkersModel();
  workersStarted: WorkersModel = new WorkersModel();
  public location: {
    lat: number
    lon: number
  } = {
      lat: null,
      lon: null
    };
  private updateIntervalHandler;
  private pageLeaveSubscription: Subscription;
  private inHandler = false;
  private siteName = "unknown";
  private supervisorName = "unknown";


  constructor(
    public nav: NavController,
    public workerService: WorkerService,
    public swmService: SwmService,
    public loadingCtrl: LoadingController,
    public sharedService: SharedService,
    private geolocation: Geolocation,
    private prestartConfigurationService: PrestartConfigurationService,
  ) {
    console.log("ClockOnPage constructor");
  }

  ionViewDidEnter() {
    console.log("ionViewDidEnter - clockOn ");
    // update service so signOff page knows about changes
    clearInterval(this.updateIntervalHandler);
    this.updateIntervalHandler = setInterval(() => {
      // Interval could be triggered multiple times in row
      if (this.inHandler) {
        return;
      }
      this.inHandler = true;
      this.updateData()
        .then(() => this.inHandler = false, () => this.inHandler = false);
    }, TAB_UPDATE_INTERVAL);
    // Sometimes ionDidLeave is not called
    this.pageLeaveSubscription = this.sharedService.onPageLeave.subscribe(() => {
      clearInterval(this.updateIntervalHandler);
    });
    this.updateData();

    // Get the site name to display
    this.siteName = this.sharedService.siteName;
    this.supervisorName = this.sharedService.loggedInUsername;

  }

  ionViewWillLeave() {
    this.pageLeaveSubscription.unsubscribe();
    clearInterval(this.updateIntervalHandler);
    console.log('Clearing ticker');
  }

  updateData() {
    const loading = this.loadingCtrl.create();
    loading.present();
    const done = () => loading.dismiss();

    this.getGeoLocation();
    this.fetchPrestart();
    return this.fetchWorkersList()
      .then(done, done);
  }

  createViewArrays() {
    // parse the workers array to provide swm compilance indication
    // and whether the worker has started work or not.
    let rqswms = this.swmService.getRequiredSwms() || [];
    console.log('Site is ', this.sharedService.siteId);
    console.log("list of required swms are ")
    console.log(rqswms)
    this.workersStarted.items = [];
    this.workersNotStartedThisSite.items = [];
    this.workerlist.items.map((worker) => {
      // do swm compliance
      console.log("print out worker")
      console.log(worker)
      worker.swmCompliant = rqswms.every((rs) =>
        worker.hasValidSwm(this.sharedService.siteId, rs) ||
        worker.hasClaimedSwm(this.sharedService.siteId, rs));

      // split workers into started and not started arrays - exclude
      // workers that are finished for the day
      if (worker.clockedOn) {
        console.log("clocked on detected " + worker.name);
        if (!worker.clockedOff) {
          console.log(worker.name + " has started work");
          if (worker.siteSelected === this.sharedService.siteId) {
            this.workersStarted.items.push(worker);
          }
        } else {
          console.log(worker.name + " has finished work, but still can check-in");
          if (worker.sitesAllowed.indexOf(this.sharedService.siteId) >= 0 ||
            this.isWorkerABackupOnSite(worker.id)
          ) {
            this.workersNotStartedThisSite.items.push(worker);
          }
        }
      } else {
        console.log("worker not started " + worker.name);
        // only add if worker is on this site, or a backup for this site
        if (worker.sitesAllowed.indexOf(this.sharedService.siteId) >= 0 ||
          this.isWorkerABackupOnSite(worker.id)
        ) {
          this.workersNotStartedThisSite.items.push(worker);
        }
      }
    });
    console.log("worker clocking")
    console.log(this.workersStarted.items)
  }

  startSignIn(worker) {
    this.getGeoLocation();
    console.log("start the signin process");
    this.nav.push(SwmPage, {
      'worker': worker,
      'siteId': this.sharedService.siteId,
      'prestart': this.prestart
    });
  }

  private fetchWorkersList() {
    return this.workerService
      .getData()
      .then(data => {
        this.workerlist.items = data;
        console.log("got the worker data from service")
        console.log(this.workerlist.items)
        // process the workerlist for swm compliance and statuses
        this.createViewArrays();
        this.workerService.setWorkersStatus(this.workerlist.items);
      })
  }

  private fetchPrestart() {
    this.prestart = this.prestartConfigurationService.getBySiteId(this.sharedService.siteId);
  }

  private getGeoLocation() {
    this.geolocation.getCurrentPosition().then((resp) => {
      console.log("Location: lat = " + resp.coords.latitude + " long " + resp.coords.longitude);
      this.location.lat = resp.coords.latitude;
      this.location.lon = resp.coords.longitude;
    }).catch((error) => {
      console.log('Error getting location', error);
    });
  }

  private isWorkerABackupOnSite(worker: number): boolean {
    return this.prestart.backupStaff.indexOf(worker) >= 0;
  }
}



