import * as moment from 'moment';

import { Component, HostBinding, Input } from '@angular/core';

import { AuthService } from '../../providers/auth';
import { TimesheetModel } from '../../classes/timesheet.model';
import { TimesheetService } from '../../providers/timesheet.service';
import { WorkerModel } from '../../classes/worker.model';

@Component({
  selector: 'timesheet-item',
  templateUrl: 'timesheet-item.component.html',
})
export class TimesheetItemComponent {
  @Input() public timesheet: TimesheetModel;
  @Input() public workers: WorkerModel[] = [];
  @Input() public loaded = false;
  @Input() public editable: boolean = false;
  @HostBinding('class.timesheet') public timesheetClass = true;

  constructor(private auth: AuthService, private service: TimesheetService) { }

  getUserName(timesheet: TimesheetModel) {
    if (this.auth.isSupervisor) {
      const worker = this.workers.find((w: WorkerModel) => w.id === timesheet.user);
      return worker.name;
    } else {
      return this.auth.name;
    }
  }

  getFormattedTime(date: Date | null) {
    if (date) {
      const formatedDate = moment(date).local().format('YYYY-MM-DDTHH:mm:ss');
      return formatedDate;
    } else {
      return '';
    }
  }

  updateClockOnTime(timesheet: TimesheetModel, value: string) {
    const oldDate = timesheet.roundedClockOn ? timesheet.roundedClockOn : new Date();
    let matches;
    if (matches = /(\d+):(\d+)/.exec(value)) {
      timesheet.roundedClockOn = new Date(moment(oldDate).local().hours(+matches[1]).minutes(+matches[2]).toISOString());
    } else {
      console.error('Invalid date value for clockOn ' + value);
    }
  }

  updateClockOffTime(timesheet: TimesheetModel, value: string) {
    const oldDate = timesheet.roundedClockOff ? timesheet.roundedClockOff :
      (timesheet.roundedClockOn ? timesheet.roundedClockOn : new Date());
    let matches;
    if (matches = /(\d+):(\d+)/.exec(value)) {
      timesheet.roundedClockOff = new Date(moment(oldDate).local().hours(+matches[1]).minutes(+matches[2]).toISOString());
    } else {
      console.error('Invalid date value for clockOn ' + value);
    }
  }

  getStartDate(timesheet: TimesheetModel) {
    return timesheet.roundedClockOn ? moment(timesheet.roundedClockOn).local().format('h:mm a') : '';
  }

  getEndDate(timesheet: TimesheetModel) {
    return timesheet.roundedClockOff ? moment(timesheet.roundedClockOff).local().format('h:mm a') : '';
  }

  getDuration(timesheet: TimesheetModel) {
    if (timesheet.roundedClockOn && timesheet.roundedClockOff) {
      const diff = moment(timesheet.roundedClockOff).diff(moment(timesheet.roundedClockOn))
      const d = moment.duration(diff);
      return `${d.hours()} hrs ${d.minutes()} m`
    } else {
      return '';
    }
  }

  isApprovable(timesheet: TimesheetModel) {
    if (typeof timesheet.isApprovable === 'undefined') {
      timesheet.isApprovable = !timesheet.approved && this.editable;
    }
    return timesheet.isApprovable && this.editable;
  }

  toggleApprove(timesheet: TimesheetModel) {
    if (!this.editable) {
      return;
    }
    timesheet.approved = timesheet.approved ? 0 : 1;
  }

  toggleLunch(timesheet: TimesheetModel) {
    if (!this.editable) {
      return;
    }
    timesheet.state = timesheet.state ? 0 : 1;
  }

  toggleExtendedHours(timesheet: TimesheetModel) {
    if (!this.editable) {
      return;
    }
    timesheet.extendedHours = !timesheet.extendedHours;
    this.service.roundTimesheet(timesheet);
  }

}



