import { Component, HostBinding, Input, OnInit } from '@angular/core';

import { DailyTimesheet } from '../../classes/timesheet.model';
import { WorkerModel } from '../../classes/worker.model';

@Component({
  selector: 'timesheet',
  template:
    `
    <ion-list>
    <ion-item *ngFor="let day of items"
      class="day">
      <div class="day__title">
        {{ day.dateLabel }}
      </div>
      <timesheet-item [timesheet]="timesheet"
        [workers]="workers"
        [loaded]="loaded"
        [editable]="editable"
        *ngFor="let timesheet of day.timesheets"
      ></timesheet-item>
    </ion-item>
    <ion-item class="empty-timesheet" *ngIf="!items.length && loaded">
      No results, try another day/worker/site filter
    </ion-item>
  </ion-list>
  `
})

export class TimesheetComponent {

  @Input() public items: DailyTimesheet[] = [];
  @Input() public workers: WorkerModel[] = [];
  @Input() public loaded = false;
  @HostBinding('class.editable') @Input() public editable: boolean = false;

}



