import 'rxjs/add/operator/map';

import * as moment from 'moment';

import { ApiService } from './api';
import { ErrorNotAuthenticated } from '../classes/exceptions/not-authenticated.error';
import { Injectable } from '@angular/core';
import { WorkerModel } from '../classes/worker.model';

export const DEFAULT_PERSON = './assets/images/profile/unknown-person.png';

/*
  Generated class for the Worker provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class WorkerService {

  workers: WorkerModel[] = [];

  constructor(private api: ApiService) {
  }

  getData(): Promise<Array<WorkerModel>> {
    // just mock data for now but not clear how the list of workers will be
    // generated on the server (ie prepopulated by head office, filtered on
    // supervisior etc)
    console.log("getData workers");
    return this.api.get('/v/1/0/staff')
      .toPromise()
      .then(response => response.json())
      .then(({ staff }) => (
        staff.map(
          ({
            id,
            name,
            image,
            message,
            validSwms,
            claimedSwms,
            passcode,
            siteSelected,
            clockedOn,
            clockedOff,
            supervisor,
            allowedSites,
            siteVisits,
            created_at,
            updated_at
          }) => new WorkerModel({
              id,
              name,
              image,
              passcode,
              description: message,
              validSwms,
              clockOn: clockedOn ? moment(clockedOn).format('HH:mm:ss') : '',
              clockedOn: clockedOn !== null,
              clockOff: clockedOff ? moment(clockedOff).format('HH:mm:ss') : '',
              clockedOff: clockedOff !== null,
              startedWork: clockedOn !== null,
              finishedWork: clockedOff !== null,
              supervisor: !!supervisor,
              sitesAllowed: allowedSites,
              siteSelected: siteSelected !== null ? siteSelected : 0,
              siteVisits: siteVisits.map(({ id, checkInDate, checkOutDate }) => ({
                id,
                checkInDate: checkInDate ? new Date(checkInDate) : null,
                checkOutDate: checkOutDate ? new Date(checkOutDate) : null,
              })),
              claimedSwms,
              // not returned from server
              swms: [],
              swmCompliant: true,
              tasks: [],
              clockOnAddress: '',
              clockOffAddress: '',
              date: new Date(updated_at),
              ackSwms: true,
              ackIssues: true,
              inLocation: true
            })
        )
      ))
      .then((workers: WorkerModel[]) => {
        // store locally
        this.workers = workers;
        return workers;
      })
      .catch(this.handleError);
  }

  approveSwm(swmId: number, siteId: number, userId: number) {
    return this.api
      .post('/v/1/0/staff/swm-approval', { swm: swmId, site: siteId, user: userId })
      .toPromise()
      .then(response => response.json())
  }

  private handleError(error: any): any {
    if (error instanceof ErrorNotAuthenticated) {
      return [];
    }
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  setWorkersStatus(workers: WorkerModel[]) {
    this.workers = workers;
  }

  getWorkersStatus(): WorkerModel[] {
    return this.workers;
  }

}



