import 'rxjs/add/operator/map';

import * as moment from 'moment';

import { DailyTimesheet, TimesheetModel } from '../classes/timesheet.model';

import { ApiService } from './api';
import { Injectable } from '@angular/core';
import { Site } from '../classes/site.model';
import { SiteService } from './sites';

const DEFAULT_TIMEZONE = 10;

@Injectable()
export class TimesheetService {

  timesheets: TimesheetModel[] = [];
  dailyTimesheets: DailyTimesheet[] = [];

  constructor(
    private api: ApiService,
    private sites: SiteService
  ) { }

  public getDailyTimesheetsForSite(
    siteId: number,
    filterValidated: boolean = false
  ): DailyTimesheet[] {
    return this.dailyTimesheets
      .map((day: DailyTimesheet) => new DailyTimesheet(
        day.date,
        day.dateLabel,
        day.timesheets.filter((t) =>
          t.site === siteId &&
          (!filterValidated || !t.approved)
        )
      ))
      .filter((day: DailyTimesheet) => !!day.timesheets.length);
  }

  public reset() {
    this.timesheets = [];
    this.dailyTimesheets = [];
  }

  public fetch(options?: any): Promise<DailyTimesheet[]> {
    console.log("getData timesheets")
    return this.api.get('/v/1/0/timesheet', options)
      .toPromise()
      .then(response => response.json())
      .then(({ timesheets }) => (
        timesheets.map(
          ({
            id,
            user,
            site,
            clockOn,
            clockOff,
            state,
            approved
          }) => new TimesheetModel(
            id,
            user,
            site,
            clockOn ? new Date(clockOn) : null,
            clockOff ? new Date(clockOff) : null,
            state,
            approved
          )
        )
      ))
      .then((timesheets: TimesheetModel[]) => {
        timesheets.forEach((t) => t.isApprovable = !t.approved);
        timesheets.forEach((t) => this.roundTimesheet(t));
        timesheets.sort((a, b) => b.clockOn.getTime() - a.clockOn.getTime());
        this.timesheets = this.timesheets.concat(...timesheets);
        const newDaySheets = this.splitDailyTimesheets(timesheets);
        this.dailyTimesheets = this.dailyTimesheets.concat(...newDaySheets);
        return this.dailyTimesheets;
      })
      .catch(this.handleError);
  }

  public approve(timesheet: TimesheetModel): Promise<void> {
    return this.api
      .post(`/v/1/0/timesheet/${timesheet.id}/confirm`, {
        checkIn: timesheet.roundedClockOn ? timesheet.roundedClockOn.toISOString() : null,
        checkOut: timesheet.roundedClockOff ? timesheet.roundedClockOff.toISOString() : null,
        took_lunch: timesheet.state === 1
      })
      .toPromise()
  }

  public roundTimesheet(timesheet: TimesheetModel) {
    const site = this.sites.byId(timesheet.site);
    if (!timesheet.isApprovable) {
      // Do not modify allready approved timesheet
      timesheet.roundedClockOn = timesheet.clockOn;
      timesheet.roundedClockOff = timesheet.clockOff;
    } else if (timesheet.extendedHours){
      // Don't use site hours - just the round to the 5min mark
      timesheet.roundedClockOn = this.roundNext5Min(timesheet.clockOn);
      timesheet.roundedClockOff = this.roundPrev5Min(timesheet.clockOff);

    } else {
      const { openTime, closeTime } = this.getDefaultTimesForSite(site, timesheet.clockOn);
      timesheet.roundedClockOn = this.roundClockOn(timesheet, openTime);
      timesheet.roundedClockOff = this.roundClockOff(timesheet, closeTime);
      // if clock on > clock off, set them equal
      if (
        timesheet.roundedClockOn &&
        timesheet.roundedClockOff &&
        timesheet.roundedClockOn.getTime() > timesheet.roundedClockOff.getTime()
      ) {
        timesheet.roundedClockOff = timesheet.roundedClockOn;
      }
    }
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

  private splitDailyTimesheets(timesheets: TimesheetModel[]): DailyTimesheet[] {
    const result = [];
    if (!timesheets.length) {
      return [];
    }
    let day = new DailyTimesheet(null, '', []);
    let lastDt = '';
    for (let timesheet of timesheets) {
      const dt = this.formatDateLabel(timesheet);
      if (dt !== lastDt) {
        if (day.timesheets.length) {
          result.push(day);
        }
        day = new DailyTimesheet(timesheet.clockOn, dt, [timesheet]);
      } else {
        day.timesheets.push(timesheet);
      }
      lastDt = dt;
    }
    if (day.timesheets.length) {
      result.push(day);
    }
    return result;
  }

  private formatDateLabel(timesheet: TimesheetModel) {
    return moment(timesheet.clockOn).format('dddd (MMM Do YY)');
  }

  private roundClockOn(timesheet: TimesheetModel, openTime: Date | null): Date | null {
    if (!timesheet.clockOn) {
      return null;
    }
    if (openTime) {
      if (openTime.getTime() > timesheet.clockOn.getTime() && !timesheet.approved) {
        // site is not open yet, and extended hours not enabled
        return new Date(openTime);
      } else {
        // site is open
        return this.roundNext5Min(timesheet.clockOn);
      }
    } else {
      return this.roundNext5Min(timesheet.clockOn);
    }
  }

  private roundClockOff(timesheet: TimesheetModel, closeTime: Date | null): Date | null {
    if (!timesheet.clockOff) {
      return null;
    }
    if (closeTime) {
      if (closeTime.getTime() < timesheet.clockOff.getTime() && !timesheet.approved) {
        // site is not open yet, and extended hours not enabled
        return new Date(closeTime);
      } else {
        // site is open
        return this.roundPrev5Min(timesheet.clockOff);
      }
    } else {
      return this.roundPrev5Min(timesheet.clockOff);
    }
  }

  private getDefaultTimesForSite(site: Site, day: Date): {
    openTime: Date | null,
    closeTime: Date | null
  } {
    if (!site) {
      return { openTime: null, closeTime: null };
    }
    return {
      openTime: this.expandDayTime(site.dayStart, day),
      closeTime: this.expandDayTime(site.dayEnd, day),
    }
  }

  /**
   * Convert string like "10:11" to a starting time of current date with a default timezone
   * @param time
   */
  private expandDayTime(time: string, day: Date): Date | null {
    if (!time) {
      return null;
    }
    let matches;
    if (matches = /^(\d+):(\d+)/.exec(time)) {
      const hours = +matches[1];
      const minutes = +matches[2];
      return new Date(moment(day).utcOffset(DEFAULT_TIMEZONE).hour(hours).minutes(minutes).format());
    } else {
      return null;
    }
  }

  private roundNext5Min(date: Date): Date {
    if (!date) {
      return null;
    }
    const year = date.getFullYear();
    const month = date.getMonth();
    const day = date.getDate();
    const hour = date.getHours();
    let minutes = date.getMinutes();
    const ofs = minutes % 5;
    if (ofs > 0) {
      minutes = minutes + (5 - ofs);
    }
    return new Date(year, month, day, hour, minutes);
  }

  private roundPrev5Min(date: Date): Date {
    if (!date) {
      return null;
    }
    const year = date.getFullYear();
    const month = date.getMonth();
    const day = date.getDate();
    const hour = date.getHours();
    let minutes = date.getMinutes();
    const ofs = minutes % 5;
    if (ofs > 0) {
      minutes = minutes - ofs;
    }
    return new Date(year, month, day, hour, minutes);
  }
}



