import { Component, ElementRef, Input, OnChanges, Renderer, SimpleChange } from '@angular/core';

import { isPresent } from 'ionic-angular/util/util';

@Component({
  selector: 'preload-image',
  templateUrl: 'preload-image.html'
})
export class PreloadImage implements OnChanges {
  _src: string = '';
  _ratio: { w: number, h: number };
  _img: HTMLImageElement;
  _loadHandler: any;
  _loaded: boolean = false;
  public get loaded(): boolean {
    return this._loaded;
  }

  constructor(public _elementRef: ElementRef, public _renderer: Renderer) {
    this._img = new Image();

    this._loadHandler = () => {
      this._elementRef.nativeElement.appendChild(this._img);
      this.setLoaded(true);
    };
  }

  @Input() alt: string;

  @Input() title: string;

  @Input() set src(val: string) {
    this._src = isPresent(val) ? val : '';
  }

  @Input() set ratio(ratio: { w: number, h: number }) {
    this._ratio = ratio || null;
  }

  // I'm not sure in default logic to keep ratio
  @Input() keepRatio = true;

  ngOnChanges(changes: { [propName: string]: SimpleChange }) {
    if (this.keepRatio) {
      let ratio_height = (this._ratio.h / this._ratio.w * 100) + "%";
      // Conserve aspect ratio (see: http://stackoverflow.com/a/10441480/1116959)
      this._renderer.setElementStyle(this._elementRef.nativeElement, 'padding-bottom', ratio_height);
    }
    if (changes.src) {
      this.update();
    }
  }

  private update() {
    if (isPresent(this.alt)) {
      this._img.alt = this.alt;
    }
    if (isPresent(this.title)) {
      this._img.title = this.title;
    }

    // Do not duplicate load handler
    this._img.removeEventListener('load', this._loadHandler);
    this._img.addEventListener('load', this._loadHandler);

    this._img.removeEventListener('error', this._loadHandler);
    this._img.addEventListener('error', this._loadHandler);

    this._img.src = this._src;

    this.setLoaded(false);
  }

  private setLoaded(isLoaded: boolean) {
    this._elementRef.nativeElement.classList[isLoaded ? 'add' : 'remove']('img-loaded');
  }
}



