import 'rxjs/add/operator/toPromise';

import { ApiService } from './api';
import { Injectable } from '@angular/core';
import { Site } from '../classes/site.model';

@Injectable()
export class SiteService {
  private _sites: Site[] = [];
  public get sites(): Site[] {
    return this._sites;
  }

  constructor(public api: ApiService) {
  }

  public byId(id: number): Site {
    return this._sites.find((v) => v.id === id);
  }

  public getData(): Promise<Site[]> {
    console.log("getData sites");
    return this.api.get('/v/1/0/sites')
      .toPromise()
      .then(response => response.json())
      .then(({ sites }) => (
        sites.map(
          ({
            id,
            label,
            created_at,
            updated_at,
            open,
            open_at,
            dayStart,
            dayEnd
          }) => new Site(
            id,
            label,
            open,
            open_at ? new Date(open_at) : null,
            this.parseTime(dayStart),
            this.parseTime(dayEnd)
          )
        )
      ))
      .then((sites: Site[]) => {
        // store locally
        this._sites = sites;
        return sites;
      })
      .catch(this.handleError);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    this._sites = [];
    return Promise.reject(error.message || error);
  }

  private parseTime(time: string): string | null {
    // TODO: parse a time. What about a timezone?
    return time ? time : null;
  }
}



