import { App, MenuController, Nav, Platform } from 'ionic-angular';
import { Component, ViewChild } from '@angular/core';

import { AuthService } from '../providers/auth';
import { EquipmentPage } from '../pages/equipment/equipment';
import { LoginPage } from '../pages/login/login';
import { SettingsPage } from '../pages/settings/settings';
import { SharedService } from '../providers/shared';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { TabsNavigationPage } from '../pages/tabs-navigation/tabs-navigation';
import { TimesheetHistoryPage } from '../pages/timesheet-history/timesheet-history';
import { TimesheetsPage } from '../pages/timesheets/timesheets';
import { WorkersPage } from '../pages/workers/workers'

@Component({
  selector: 'app-root',
  templateUrl: 'app.html'
})
export class MyApp {

  @ViewChild(Nav) nav: Nav;

  // make WalkthroughPage the root (or first) page
  //rootPage: any = WalkthroughPage;
  //rootPage: any = TabsNavigationPage;
  rootPage: any = LoginPage;

  pages: Array<{ title: string, icon: string, component: any, supervisor?: boolean }>;
  pushPages: Array<{ title: string, icon: string, component: any, supervisor?: boolean }>;

  // lennylu: string;
  appGlobals: any;

  constructor(
    public platform: Platform,
    public menu: MenuController,
    public app: App,
    public splashScreen: SplashScreen,
    public statusBar: StatusBar,
    public sharedService: SharedService,
    public auth: AuthService,
  ) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.splashScreen.hide();
      this.statusBar.styleDefault();
    });

    this.pages = [
      { title: 'Home', icon: 'home', component: TabsNavigationPage },
    ];

    this.pushPages = [
      { title: 'Timesheets', icon: 'grid', component: TimesheetHistoryPage, supervisor: false },
      { title: 'Timesheets', icon: 'grid', component: TimesheetsPage, supervisor: true },
      { title: 'Workers', icon: 'people', component: WorkersPage, supervisor: true },
      { title: 'Equipment', icon: 'pricetag', component: EquipmentPage },
      { title: 'Settings', icon: 'settings', component: SettingsPage }
    ];

    this.appGlobals = sharedService.getLenny();
  }

  openPage(page) {
    this.sharedService.onPageLeave.next();
    // close the menu when clicking a link from the menu
    this.menu.close();
    // navigate to the new page if it is not the current page
    this.nav.setRoot(page.component);
  }

  pushPage(page) {
    // close the menu when clicking a link from the menu
    this.menu.close();
    // rootNav is now deprecated (since beta 11) (https://forum.ionicframework.com/t/cant-access-rootnav-after-upgrade-to-beta-11/59889)
    this.app.getActiveNav().push(page.component);
  }

  showPage(p) {
    if (p.hasOwnProperty('supervisor')) {
      // the page supervisor property may be set to false
      return this.appGlobals.supervisor === p.supervisor;
    }
    // if page has no supervisor property or the page has supervisor prop false
    // then anyone can see it.
    return true;
  }

  public logout() {
    this.sharedService.onPageLeave.next();
    this.auth.reset();
    this.menu.close();
    this.nav.setRoot(LoginPage);
  }
}



