import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { SharedService } from '../../providers/shared';
import { TimesheetHistoryPage } from '../timesheet-history/timesheet-history';
import { TimesheetVerifyPage } from '../timesheet-verify/timesheet-verify';

@Component({
  selector: 'timesheet-page',
  templateUrl: 'timesheets.html'
})
export class TimesheetsPage {
  items: Array<{title: string, note?: string, component: any, supervisor?: boolean}>;
  appGlobals: any;

  constructor(public nav: NavController,
              private sharedService: SharedService) {
    this.items = [
      { title: 'Verify', component: TimesheetVerifyPage, supervisor: true },
      { title: 'History', component: TimesheetHistoryPage },
    ];

    this.appGlobals = this.sharedService.getLenny();
  }

  itemTapped(event, item) {
    this.nav.push(item.component);
  }

  showItem(p) {
    // console.log("page is " + p.title);
    if (p.hasOwnProperty('supervisor')) {
      // the page supervisor property may be set to false
      if (p.supervisor) return this.appGlobals.supervisor;
    }
    // if page has no supervisor property or the page has supervisor prop false
    // then anyone can see it.
    return true;
  }
}



