import 'rxjs/add/operator/map';

import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { UserRole } from './auth';

/*
  Generated class for the Tudomundo provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class SharedService {
  public siteId: number = null;
  public siteName: string = null;
  // For prestart
  public siteOpened: boolean = false;
  public supervisorRole: boolean = false;
  public userHelperMode: boolean = true;
  public loggedInUsername: string = null;
  //
  public onPageLeave = new Subject<void>();

  dvtGlobals = {
    supervisor: false,
    role: UserRole.Staff,
    lvtDebug: "lenny1",
    username: "freddy"
  }

  setUserRole(isSupervisor: boolean) {
    this.supervisorRole = isSupervisor;
  }

  getLenny() {
    return this.dvtGlobals;
  }

  setLenny(isSupervisor: boolean, role: UserRole, dbgStr) {
    this.dvtGlobals.role = role;
    this.dvtGlobals.supervisor = isSupervisor;
    this.dvtGlobals.lvtDebug = dbgStr;
  }

  // Single pass semaphore
  // You can trigger it to allow user a single pass to prestart page after succesful login
  public getSinglePass(): boolean {
    const ret = this._singlePass;
    this._singlePass = false;
    return ret;
  }

  public triggerSinglePass() {
    this._singlePass = true;
  }

  private _singlePass: boolean = false;
}



