import { AuthService, UserRole } from '../../providers/auth';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { NavController, ToastController } from 'ionic-angular';
import { WorkerModel, WorkersModel } from '../../classes/worker.model';

import { ApiService } from '../../providers/api';
import { Component } from '@angular/core';
import { ForgotPasswordPage } from '../forgot-password/forgot-password';
import { SharedService } from '../../providers/shared';
import { SignupPage } from '../signup/signup';
import { TabsNavigationPage } from '../tabs-navigation/tabs-navigation';
import { WorkerClockonPage } from '../worker-clockon/worker-clockon';
import { WorkerService } from '../../providers/worker';

@Component({
  selector: 'login-page',
  templateUrl: 'login.html'
})
export class LoginPage {
  inprogress: boolean = false;
  login: FormGroup;
  main_page: { component: any };
  worker_login: { component: any };
  workerlist: WorkersModel = new WorkersModel();

  constructor(public nav: NavController,
    private sharedService: SharedService,
    private workerService: WorkerService,
    private auth: AuthService,
    private api: ApiService,
    private toastCtrl: ToastController
  ) {
    this.main_page = { component: TabsNavigationPage };
    this.worker_login = { component: WorkerClockonPage };

    this.login = new FormGroup({
      email: new FormControl('site_staff.1@willow.com.au', Validators.required),
      password: new FormControl('secret', Validators.required)
    });

    this.api.loginRedirectHandler = () => {
      this.sharedService.onPageLeave.next();
      this.nav.setRoot(LoginPage);
    }
  }

  doLogin() {
    this.inprogress = true;
    this.auth.login(this.login.value.email, this.login.value.password)
      .subscribe((loggedIn: boolean) => {
        this.inprogress = false;
        this.notifyOnLogin(loggedIn);

        if (loggedIn) {
          this.sharedService.siteId = 0;
          this.sharedService.loggedInUsername = this.auth.name;
          const isSupervisor = this.auth.role === UserRole.Supervisor;
          this.sharedService.setLenny(isSupervisor, this.auth.role, this.login.value.email)
          if (isSupervisor) {
            console.log('Login: Supervisor');
            this.workerService
              .getData()
              .then(data => this.workerlist.items = data);

            // allow single time pass without a password to restricted area
            this.sharedService.onPageLeave.next();
            this.sharedService.triggerSinglePass();
            this.nav.setRoot(this.main_page.component);
          } else {
            this.inprogress = false;
            console.log('Login: Worker');
            // TODO: Replace with a real data, when backend service will be ready
            let worker: WorkerModel = this.auth.worker;
            console.log('Worker is', Object.assign({}, worker));
            this.sharedService.onPageLeave.next();
            this.nav.setRoot(this.worker_login.component, { 'worker': worker })
          }
        }
      });
  }

  doFacebookLogin() {
    this.sharedService.onPageLeave.next();
    this.nav.setRoot(this.main_page.component);
  }

  doGoogleLogin() {
    this.sharedService.onPageLeave.next();
    this.nav.setRoot(this.main_page.component);
  }

  goToSignup() {
    this.nav.push(SignupPage);
  }

  goToForgotPassword() {
    this.nav.push(ForgotPasswordPage);
  }

  private notifyOnLogin(loggedIn) {
    let toast = this.toastCtrl.create({
      message: loggedIn ? 'User successfully logged in' : 'Invalid login or password',
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

}



