import * as bcrypt from 'bcryptjs';

import { Headers, Http, Response } from "@angular/http";

import { API_URL } from "../environment/env";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { UserModel } from '../pages/profile/profile.model';
import { WorkerModel } from '../classes/worker.model';

export enum UserRole {
  Supervisor,
  Staff
}

const EMPTY_WORKER = new WorkerModel({
  id: 0,
  name: '',
  passcode: '',
  image: '',
  description: '',
  swms: [],
  validSwms: [],
  claimedSwms: [],
  swmCompliant: false,
  tasks: [],
  clockOn: '',
  clockedOn: false,
  clockOnAddress: '',
  clockOff: '',
  clockOffAddress: '',
  clockedOff: false,
  startedWork: false,
  finishedWork: false,
  date: new Date(),
  ackSwms: false,
  ackIssues: false,
  siteSelected: 0,
  inLocation: false,
  siteVisits: [],
  sitesAllowed: [],
  supervisor: false
});

const EMPTY_USER = {
  image: '',
  location: '',
  about: '',
  email: '',
  phone: '',
  name: '',
};

@Injectable()
export class AuthService {
  private _loggedIn: boolean = false;
  private _apiKey: string = null;
  private _role: UserRole = null;
  private _hashedPassword: string = null;
  private _siteVisits: any = null;
  private _name: string = null;
  private _image: string = null;
  private _user: UserModel = Object.assign({}, EMPTY_USER);
  private _worker: WorkerModel = new WorkerModel(Object.assign({}, EMPTY_WORKER));

  public get loggedIn() {
    return this._loggedIn;
  }

  public get apiKey(): string {
    return this._apiKey;
  }

  public get role(): UserRole {
    return this._role;
  }

  public get isSupervisor(): boolean {
    return this._role === UserRole.Supervisor;
  }

  public get siteVisits(): any {
    return this._siteVisits;
  }

  public get name(): string {
    return this._name;
  }

  public get image(): string {
    return this._image;
  }

  public get user(): UserModel {
    return this._user;
  }

  public get worker(): WorkerModel {
    return this._worker;
  }

  public reset() {
    this._loggedIn = false;
    this._apiKey = null;
    this._role = null;
    this._hashedPassword = null;
    this._siteVisits = null;
    this._name = null;
    this._image = null;
    this._worker.validSwms = [];
    this._user = Object.assign({}, EMPTY_USER);
    this._worker = new WorkerModel(Object.assign({}, EMPTY_WORKER));
  }

  constructor(private http: Http) { }

  public login(username: string, password: string): Observable<boolean> {
    const headers = {
      headers: new Headers({ 'Content-type': 'application/json' })
    };
    return this.http.post(API_URL + '/login', JSON.stringify({ email: username, password }), headers)
      .map((response: Response): boolean => {
        const res = response.json().data;
        this._loggedIn = true;
        this._apiKey = res['API Key'];
        this._role = res.profile.role === 'supervisor' ? UserRole.Supervisor : UserRole.Staff;
        this._hashedPassword = res.profile.password_hash;
        this._siteVisits = res.profile.sites;
        this._name = res.profile.name;
        this._image = res.profile.image;
        // Fill user model
        this._user.name = this._name;
        this._user.image = this._image;
        // Fill worker model
        this._worker.name = this._name;
        this._worker.image = this._image;
        this._worker.supervisor = this._role === UserRole.Supervisor;
        this._worker.siteVisits = (this._siteVisits || [])
          .map(({ id, checkInDate, checkOutDate }) => ({
            id,
            checkInDate: checkInDate ? new Date(checkInDate) : null,
            checkOutDate: checkOutDate ? new Date(checkOutDate) : null,
          }));
        this._worker.validSwms = res.profile.validSwms;
        this._worker.claimedSwms = res.profile.claimedSwms;
        this._worker.sitesAllowed = res.profile.allowedSites || [];

        return true;
      })
      .catch((error) => {
        console.error(error);
        this._loggedIn = false;
        this._apiKey = null;
        this._role = null;
        this._siteVisits = [];
        return Observable.of(false);
      })
  }

  public checkPassword(password): boolean {
    if (!this._hashedPassword) {
      return false;
    }
    return bcrypt.compareSync(password, this._hashedPassword);
  }
}



