import { ApiService } from "./api";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { Response } from "@angular/http";

interface CheckinResponse {
  success: boolean;
  message?: string; // for failed requests
  in_location?: boolean;
}

interface CheckoutResponse {
  success: boolean;
  message?: string; // for failed requests
}

@Injectable()
export class CheckInOutService {
  constructor(private api: ApiService) { }

  public checkIn(jobId, latitude, longitude, userId?): Observable<CheckinResponse> {
    if (!this.api.checkAuth()) {
      return Observable.of();
    }
    let data: any = {
      jobId, latitude, longitude
    };
    if (userId) {
      data['userId'] = userId;
    }
    return this.api.post('/v/1/0/checkin', data)
      .map((res: Response): CheckinResponse => {
        const { success, message } = res.json();
        return {
          success,
          message,
          in_location: message === 'success:inl'
        };
      });
  }

  public checkOut(jobId, took_lunch, latitude, longitude, userId?): Observable<CheckoutResponse> {
    if (!this.api.checkAuth()) {
      return Observable.of();
    }
    let data: any = { jobId, latitude, longitude, took_lunch };
    if (userId) {
      data['userId'] = userId;
    }
    return this.api.post('/v/1/0/checkout', data)
      .map((res: Response): CheckinResponse => {
        const { success, message } = res.json();
        return {
          success,
          message
        };
      });
  }

}



