import 'rxjs/add/operator/toPromise';

import * as moment from 'moment';

import { ApiService } from './api';
import { Injectable } from '@angular/core';
import { SafetyRemindersModel, SafetyReminderModel } from '../classes/safety-reminder.model';

@Injectable()
export class SafetyReminderService {
  constructor(public api: ApiService) {
    console.log('Hello Safety Focus Provider');
  }

  // this gets the data from the server - just the swm definitions .. .therefore
  // are no live status in this data.
  getData(userId?): Promise<SafetyRemindersModel> {
    console.log("getData safety focus data");
    // original mock './assets/example_data/safety-focus.json'
    return this.api.get('/v/1/0/safety-reminders', userId ? { userId } : null)
      .toPromise()
      .then(response => response.json())
      .then(({ safetyReminders }) => (
        {
          items: safetyReminders.map(
            ({ id, title, text, description, image, updated_at }) => ({
              id,
              name: title,
              description,
              details: text,
              image: image || './assets/images/safety-focus/bigstock-Accident-At-Work-53124226.jpg',
              date: updated_at ? moment(new Date(updated_at)).format('MMM Do') : '',
              // Fields that not provided by backend, using stubs for now
              actions: []
            })
          )
        }
      ))
      .catch(this.handleError);
  }

  reset(reminder: SafetyReminderModel) {
    return this.api.post(`/v/1/0/safety-reminders/${reminder.id}/reset`, {})
      .toPromise()
      .then(response => response.json())
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}



