import { NavController, NavParams } from 'ionic-angular';

import { Component } from '@angular/core';
import { SafetyReminderModel } from '../../classes/safety-reminder.model';
import { WorkerModel } from '../../classes/worker.model';

@Component({
  selector: 'page-safety-reminder-detail',
  templateUrl: 'safety-reminder-detail.html'
})
export class SafetyReminderDetailPage {

  worker: WorkerModel;
  safetyReminder: SafetyReminderModel = new SafetyReminderModel();
  actionsExist: boolean = false;
  sfAccepted: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.worker = this.navParams.get("worker");
    this.safetyReminder = this.navParams.get("safetyReminder");
    if (typeof this.safetyReminder.actions !== 'undefined' && this.safetyReminder.actions !== "") {
      this.actionsExist = true;
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SafetyReminderDetailPage');
  }
}



