interface PrestartConfigurationInterface {
  siteId: number;
  tasks: number[];
  swms: number[];
  reminders: number[];
  issues: number[];
  backupStaff: number[];
}

export class PrestartConfiguration implements PrestartConfigurationInterface {
  constructor(
    public siteId: number,
    public tasks: number[] = [],
    public swms: number[] = [],
    public reminders: number[] = [],
    public issues: number[] = [],
    public backupStaff: number[] = [],
  ) { }
}



