import 'rxjs/Rx';

import { AlertController, LoadingController, NavController, SegmentButton, ToastController } from 'ionic-angular';

import { AuthService } from '../../providers/auth';
import { Component } from '@angular/core';
import { IssueService } from '../../providers/issue';
import { IssuesModel } from '../../classes/issue.model';
import { PrestartConfiguration } from '../../classes/prestart-configuration.model';
import { PrestartConfigurationService } from '../../providers/prestart-configuration';
import { SafetyReminderDetailPage } from '../safety-reminder-detail/safety-reminder-detail';
import { SafetyReminderService } from '../../providers/safety-reminder';
import { SafetyRemindersModel, SafetyReminderModel } from '../../classes/safety-reminder.model';
import { SharedService } from '../../providers/shared';
import { Site } from '../../classes/site.model';
import { SiteService } from '../../providers/sites';
import { SwmDetailPage } from '../swm-detail/swm-detail';
import { SwmListEditPage } from '../swm-list-edit/swm-list-edit';
import { SwmService } from '../../providers/swm';
import { SwmsModel } from '../../classes/swm.model';
import { TaskService } from '../../providers/task';
import { TasksModel } from '../../classes/task.model';

@Component({
  selector: 'prestart-page',
  templateUrl: 'prestart.html'
})
export class PrestartPage {
  section: string;
  swmlist: SwmsModel = new SwmsModel();
  activeSWMs: SwmsModel = new SwmsModel(); // these are determined from the tasks
  siteTaskList: TasksModel = new TasksModel();
  allTasks: TasksModel = new TasksModel();
  requiredSWMids: number[] = [];
  swmsGenerated = false;
  loading: boolean = false;

  safetyReminders: SafetyRemindersModel = new SafetyRemindersModel();
  safetyIssues: IssuesModel = new IssuesModel();

  categories_checkbox_open: boolean;

  constructor(public nav: NavController,
    public auth: AuthService,
    public alertCtrl: AlertController,
    public swmService: SwmService,
    public taskService: TaskService,
    public safetyReminderService: SafetyReminderService,
    public safetyIssueService: IssueService,
    public loadingCtrl: LoadingController,
    public sharedService: SharedService,
    public sitesService: SiteService,
    private prestartConfigurationService: PrestartConfigurationService,
    private toast: ToastController,
  ) {
    this.section = "task";
  }

  ionViewDidLoad() {

    console.log("ionViewDidLoad prestart")
    this.loading = true;
    const loader = this.loadingCtrl.create();
    loader.present();
    const op1 = this.swmService
      .getData()
      .then(data => this.swmlist.items = data.items);

    const op2 = this.taskService
      .getData()
      .then(data => {
        this.allTasks.items = data.items;
        this.updateSiteTasksList();
        this.swmsGenerated = false;
      });

    const op3 = this.safetyReminderService
      .getData()
      .then((sfd) => this.safetyReminders.items = sfd.items);

    const op4 = this.safetyIssueService
      .getData()
      .then((issues) => this.safetyIssues.items = issues.items);

    const op5 = this.fetchPrestart();

    const op6 = this.updateSitesList();

    const loaded = () => { loader.dismiss(); this.loading = false; };
    Promise.all([op1, op2, op3, op4, op5, op6]).then(loaded, loaded);
  }

  ionViewWillEnter() {
    console.log("entering prestart page .... recalc the active swms");
    this.generateActiveSwms()
  }


  onSegmentChanged(segmentButton: SegmentButton) {
    console.log('Segment changed to', segmentButton.value);
  }

  onSegmentSelected(segmentButton: SegmentButton) {
    console.log('Segment selected', segmentButton.value);
  }

  gotoSafetyIssueDetail(safetyReminder) {
    console.log("gotoSafetyReminderDetail")
    this.nav.push(SafetyReminderDetailPage, { 'worker': null, 'safetyReminder': safetyReminder });
  }

  // Update list of tasks based on selected site
  updateSiteTasksList() {
    if (!this.sharedService.siteId) {
      // Or set it to []]? Need to clarify ...
      this.siteTaskList.items = this.allTasks.items;
      return;
    }
    this.siteTaskList.items = this.allTasks.items
      .filter((task) => !task.sites.length || task.sites.indexOf(this.sharedService.siteId) >= 0);
  }

  getSiteId() {
    return this.sharedService.siteId;
  }

  getSiteName() {
    if (!this.sharedService.siteId) {
      return '<NOT SELECTED>';
    } else {
      return (this.sitesService.byId(this.sharedService.siteId) || { label: 'Unknown' }).label;
    }
  }

  // Generate the active swms from the active tasks
  // Save the updated swm list to the swmService for use elsewhere
  generateSwms() {
    this.generateSwmsList();
    // got next segment / tab for swms
    this.section = "swm";
  }

  generateSwmsList() {
    console.log("generate swms", this.siteTaskList.items);
    // clear the list first-child
    this.requiredSWMids = [];
    // build an array of all the swm ids required
    this.siteTaskList.items.forEach((task) => {
      // only if the task is active
      if (task.selected) {
        task.swms.forEach((id) => {
          console.log("swm id " + id)
          if (this.requiredSWMids.indexOf(id) < 0) {
            this.requiredSWMids.push(id)
          }
        })
      }
    })
    console.log(this.requiredSWMids);
    // save requiredSWMids to swmService to use later to cross reference
    // against each worker to make sure they have done the swm courses
    // required for the day's tasks
    this.swmService.setRequiredSwms(this.requiredSWMids);

    // generate a local list of just the active swms to display
    this.activeSWMs.items = this.swmlist.items.filter((swm) => {
      console.log("swm " + swm.name + " is " + swm.inuse)
      swm.inuse = false;
      // now see if swm id in the active lists
      if (this.requiredSWMids.indexOf(swm.id) >= 0) {
        swm.inuse = true;
      }
      return swm.inuse;
    })
    // update the service
    this.swmsGenerated = true;
    this.swmService.updateSwmList(this.swmlist);
  }

  generateActiveSwms() {
    // just generate a list of active swms
    this.activeSWMs.items = this.swmlist.items.filter((swm) => swm.inuse);
  }

  // Select site
  chooseCategory() {
    console.log("chooseCategory")
    let alert = this.alertCtrl.create({
      cssClass: 'category-prompt'
    });
    alert.setTitle('Site');

    this.sitesService.sites
      .filter((site) => this.auth.worker.sitesAllowed.indexOf(site.id) >= 0)
      .forEach(({ id, label }, index) => {
        alert.addInput({
          type: 'radio',
          value: '' + id,
          label,
          checked: id === this.sharedService.siteId
        });
      });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Confirm',
      handler: data => {
        console.log('Checkbox data:', data);
        this.categories_checkbox_open = false;
        this.selectSite(+data);
      }
    });
    alert.present().then(() => {
      this.categories_checkbox_open = true;
    });
  }

  swmdetails(swmid, swm) {
    console.log("goto swm details for " + swmid, 'SWM is ', swm);
    this.nav.push(SwmDetailPage, { id: swmid, swm: swm });
  }

  public onSelectItem(item): void {
    item.selected = typeof item.selected !== 'undefined' ? !item.selected : true;
  }

  editActiveSwms() {
    console.log("edit swm list");
    this.nav.push(SwmListEditPage);
  }

  isReadyForPrestart(): boolean {
    return this.swmsGenerated;
  }

  isRemindersChecked(): boolean {
    return this.safetyIssues.items.every((i) => i.selected);
  }

  prestartsComplete() {
    const prestartConfiguration = new PrestartConfiguration(
      this.sharedService.siteId,
      this.siteTaskList.items.filter(i => i.selected).map(i => i.id),
      this.swmlist.items.filter(swm => swm.inuse).map(i => i.id),
      this.safetyReminders.items.filter(i => i.selected).map(i => i.id),
      this.safetyIssues.items.filter(i => i.selected).map(i => i.id),
    );
    this.prestartConfigurationService
      .post(prestartConfiguration)
      .then(() => this.prestartConfigurationService.getData())
      .then(() => {
        this.sharedService.siteOpened = true;
        this.nav.parent.select(1);
      }, () => {
        this.notify('Server error. Try again later');
      });
  }

  refreshSites() {
    this.loading = true;
    const loader = this.loadingCtrl.create();
    loader.present();
    const done = () => {
      loader.dismiss();
      this.loading = false;
    }
    this.updateSitesList().then(done, done);
  }

  public resetReminder(reminder: SafetyReminderModel) {
    let alert = this.alertCtrl.create({
      cssClass: 'category-prompt'
    });
    alert.setTitle('Reset reminder?');
    alert.setMessage(`This will re-activate the reminder '${reminder.name}' for all workers`);
    alert.addButton('No');
    alert.addButton({
      text: 'Yes',
      handler: () => {
        const loader = this.loadingCtrl.create();
        loader.present();
        const done = () => loader.dismiss();
        this.safetyReminderService.reset(reminder).then(done, done);
      }
    });
    alert.present().then(() => {
      this.categories_checkbox_open = true;
    });
  }

  private selectSite(id: number) {
    this.sharedService.siteId = id;
    let siteName = this.getSiteName();
    // this should be the only place the site is set so we can save the name
    // to use on other pages instead of calculating it again.
    this.sharedService.siteName = siteName;
    this.applyPrestartForSite(id);
    this.updateSiteTasksList();
  }

  private fetchPrestart() {
    return this.prestartConfigurationService.getData();
  }

  private applyPrestartForSite(siteId) {
    const conf = this.prestartConfigurationService.getBySiteId(siteId);
    this.sharedService.siteOpened = !!conf;
    // Choose previously selected tasks
    const enabledTasks = conf ? conf.tasks : [];
    this.siteTaskList.items.forEach(i => i.selected = enabledTasks.indexOf(i.id) >= 0);
    // Choose previously selected reminders, select all if not specified
    const enabledReminders = conf ? conf.reminders : this.safetyReminders.items.map(i => i.id);
    this.safetyReminders.items.forEach(i => i.selected = enabledReminders.indexOf(i.id) >= 0);
    // Choose previously selected issues
    const enabledIssues = conf ? conf.issues : this.safetyIssues.items.map(i => i.id);
    this.safetyIssues.items.forEach(i => i.selected = enabledIssues.indexOf(i.id) >= 0);
    //
    // Choose previously selected swms
    const enabledSwms = conf ? conf.swms : [];
    if (conf) {
      this.generateSwmsList();
      this.swmlist.items.forEach(i => i.inuse = enabledSwms.indexOf(i.id) >= 0);
    } else {
      this.swmlist.items.forEach(i => i.inuse = false);
      this.activeSWMs.items = [];
    }
  }

  private updateSitesList() {
    return this.sitesService
      .getData()
      .then((sites: Site[]) => {
        if (sites.length === 1) {
          this.selectSite(sites[0].id);
        }
      });
  }

  private notify(text, duration = 3000) {
    const toast = this.toast.create({
      message: text,
      duration,
      position: 'top'
    });
    toast.present();
  }
}



