import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { EquipmentPrestartPage } from '../equipment-prestart/equipment-prestart';

/**
 * Generated class for the equipment page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@Component({
  selector: 'page-equipment',
  templateUrl: 'equipment.html'
})
export class EquipmentPage {
  items: Array<{title: string, note?: string, component: any}>;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.items = [
      { title: 'Prestart', component: EquipmentPrestartPage },
    ];
  }

  // ionViewDidLoad() {
  //   console.log('ionViewDidLoad Workers');
  // }

  itemTapped(event, item) {
    this.navCtrl.push(item.component);
  }

}



