import { Headers, Http, RequestOptionsArgs } from "@angular/http";

import { API_URL } from "../environment/env";
import { AuthService } from "./auth";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { ToastController } from "ionic-angular";

const MSG_SERVER_ERROR = 'Server error. Please try again.';

@Injectable()
export class ApiService {
  public loginRedirectHandler: () => void = null;

  constructor(private auth: AuthService, private http: Http, private toast: ToastController) { }

  public get(path: string, options: any = null) {
    return this.http.get(API_URL + path, this.requestHeaders(options))
      .catch((err) => this.catchCommonServerErrors(err));
  }

  public post(path: string, data: any) {
    return this.http.post(API_URL + path, JSON.stringify(data), this.requestHeaders())
      .catch((err) => this.catchCommonServerErrors(err));
  }

  public delete(path: string) {
    return this.http.delete(API_URL + path, this.requestHeaders())
      .catch((err) => this.catchCommonServerErrors(err));
  }

  public checkAuth() {
    if (!this.auth.loggedIn) {
      if (this.loginRedirectHandler) {
        this.loginRedirectHandler();
      }
      return false;
    }
    return true;
  }

  private requestHeaders(params: any = null): RequestOptionsArgs {
    return Object.assign({
      headers: new Headers({
        'Content-type': 'application/json',
        'X-Auth': this.auth.apiKey
      })
    }, params ? { params } : {});
  }

  private catchCommonServerErrors(err) {
    if (err.status === 401) {
      console.log('Got 401 server error');
      if (this.loginRedirectHandler) {
        console.log('Redirecting to login');
        this.loginRedirectHandler();
      }
    }
    if (!err.status || err.status >= 500) {
      this.notify(MSG_SERVER_ERROR);
    }
    return Observable.throw(err);
  }

  private notify(text, duration = 3000) {
    const toast = this.toast.create({
      message: text,
      duration,
      position: 'top'
    });
    toast.present();
  }
}



