import {
  AlertController,
  LoadingController,
  NavController,
  NavParams,
  ToastController
} from 'ionic-angular';

import { Component } from '@angular/core';
import { PrestartConfiguration } from '../../classes/prestart-configuration.model';
import { PrestartConfigurationService } from '../../providers/prestart-configuration';
import { Site } from '../../classes/site.model';
import { SiteService } from '../../providers/sites';
import { WorkerModel } from '../../classes/worker.model';
import { WorkerService } from '../../providers/worker';

@Component({
  selector: 'page-workers',
  templateUrl: 'workers.html'
})
export class WorkersPage {
  public siteSelected = null;
  public sites = [];
  public allWorkers = [];
  public filteredWorkers = [];
  public prestarts: PrestartConfiguration[] = [];
  public nameFilter: string = '';
  public location: {
    lat: number
    lon: number
  } = {
      lat: null,
      lon: null
    };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private workerService: WorkerService,
    private sitesService: SiteService,
    private prestartConfigurationService: PrestartConfigurationService,
    private toast: ToastController,
  ) {
  }

  ionViewDidLoad() {
    this.refresh();
  }

  public selectSite(site: Site) {
    this.siteSelected = site;
  }

  public trackWorker(worker: WorkerModel) {
    return worker.id + '-' + worker.siteSelected + '-' + worker.clockOn + '-' + worker.clockOff;
  }

  public filterWorkersList() {
    this.filteredWorkers = this.allWorkers.slice();
    if (this.siteSelected && this.siteSelected.id) {
      this.filteredWorkers = this.filteredWorkers.filter((w: WorkerModel) => w.sitesAllowed.indexOf(this.siteSelected.id) >= 0);
    }
    if (this.nameFilter) {
      const lcNameFilter = this.nameFilter.toLowerCase();
      this.filteredWorkers = this.filteredWorkers.filter((w: WorkerModel) =>
        (w.name || '').toLowerCase().indexOf(lcNameFilter) >= 0);
    }
    const sortByName = (a: WorkerModel, b: WorkerModel) => (a.name || '').localeCompare(b.name);
    this.filteredWorkers.sort(sortByName);
    return this.filteredWorkers;
  }

  public refresh() {
    const loader = this.loadingCtrl.create();
    loader.present();
    const op1 = this.updateSitesList();
    const op2 = this.fetchWorkersList();
    const op3 = this.fetchPrestartConfiguration();
    const loaded = () => { loader.dismiss(); };
    return Promise.all([op1, op2, op3]).then(loaded, loaded);
  }

  public isWorkerOnSite(worker: WorkerModel) {
    return worker.clockedOn && !worker.clockedOff;
  }

  public isWorkerLeftSite(worker: WorkerModel) {
    return worker.clockedOn && worker.clockedOff;
  }

  public workerTapped($event, worker: WorkerModel) {
    this.manageBackup(worker);
  }

  public sitesInfoFor(worker: WorkerModel): string {
    let result = '';
    if (worker.sitesAllowed.length) {
      const sitesList = worker.sitesAllowed
        .map((siteId) => {
          const site = this.sitesService.byId(siteId);
          return site ? site.label : '<Unknown>'
        })
        .join(', ');
      result = `Sites allowed: ${sitesList}.`
    }
    const backupSites = this.prestarts
      .filter((p: PrestartConfiguration) => p.backupStaff.indexOf(worker.id) >= 0)
      .map((p: PrestartConfiguration) => {
        const site = this.sitesService.byId(p.siteId);
        return site ? site.label : '<Unknown>'
      })
      .join(', ');
    if (backupSites) {
      result += `As a backup on sites: ${backupSites}`;
    }
    return result;
  }

  private updateSitesList() {
    return this.sitesService
      .getData()
      .then((sites: Site[]) => this.sites = sites,
      (error) => {
        console.error(error);
        this.notify('Server error');
      });
  }

  private fetchWorkersList() {
    return this.workerService
      .getData()
      .then((workers: WorkerModel[]) => this.allWorkers = workers,
      (error) => {
        console.error(error);
        this.notify('Server error');
      })
      .then(() => this.filterWorkersList());
  }

  private fetchPrestartConfiguration() {
    return this.prestartConfigurationService
      .getData()
      .then((data: PrestartConfiguration[]) => this.prestarts = data,
      (error) => {
        console.error(error);
        this.notify('Server error');
      });
  }

  public siteForWorker(worker: WorkerModel): Site {
    if (worker.siteSelected) {
      return this.sitesService.byId(worker.siteSelected);
    } else {
      return null;
    }
  }

  private notify(text, duration = 3000) {
    const toast = this.toast.create({
      message: text,
      duration,
      position: 'top'
    });
    toast.present();
  }

  private manageBackup(worker: WorkerModel) {
    const loader = this.loadingCtrl.create();
    loader.present();
    const loaded = () => { loader.dismiss(); };

    let alert = this.alertCtrl.create({
      cssClass: 'category-prompt'
    });
    alert.setTitle('Add to site');

    this.sitesService.sites.forEach(({ id, label, open }, index) => {
      alert.addInput({
        type: 'radio',
        value: '' + id,
        label: label + (open ? '' : '[NOT OPEN]'),
        checked: false
      });
    });

    alert.addButton({
      text: 'Cancel',
      handler: loaded
    });
    alert.addButton({
      text: 'Confirm',
      handler: data => {
        const siteId = +data;
        this.prestartConfigurationService
          .addBackupStaff(siteId, worker.id)
          .then(() => {
            this.refresh();
            loaded();
          },
          (error) => {
            this.notify('Server error');
            loaded();
          });
      }
    });
    alert.present();
  }
}



