import { AlertController, LoadingController, NavController, NavParams, ToastController } from 'ionic-angular';
import { SiteVisit, WorkerModel } from '../../classes/worker.model';

import { CheckInOutService } from '../../providers/check-in';
import { Component } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation';
import { IssueService } from '../../providers/issue';
import { IssuesModel } from '../../classes/issue.model';
import { PrestartConfiguration } from '../../classes/prestart-configuration.model';

/*
  Generated class for the SafetyIssues page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  selector: 'page-safety-issues',
  templateUrl: 'safety-issues.html'
})
export class SafetyIssuesPage {
  worker: WorkerModel;
  siteId: number;
  safetyIssues: IssuesModel = new IssuesModel();
  prestart: PrestartConfiguration;
  issueList: boolean[] = [];
  allChecked: boolean = false;
  address: string;
  public signingOn = false;

  public location: {
    lat: number
    lon: number
  } = {
      lat: null,
      lon: null
    };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public safetyIssueService: IssueService,
    public alertCtrl: AlertController,
    private geolocation: Geolocation,
    private checkInService: CheckInOutService,
    private toast: ToastController
  ) {
    this.worker = this.navParams.get('worker');
    this.siteId = +this.navParams.get('siteId');
    this.prestart = this.navParams.get('prestart');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SafetyIssuesPage');
    const loading = this.loadingCtrl.create();
    loading.present();
    const done = () => loading.dismiss();
    this.safetyIssueService
      .getData()
      .then((issues) => {
        console.log("got the safter issue data");
        this.safetyIssues.items = issues.items;
        if (this.prestart) {
          this.safetyIssues.items = this.safetyIssues.items.filter((issue) => this.prestart.issues.indexOf(issue.id) >= 0);
        }
        this.allChecked = this.safetyIssues.items.every(i => i.selected) || !this.safetyIssues.items.length;
      })
      .then(done, done);
    this.getGeoLocation();
  }

  clockOn() {
    if (!this.location) {
      this.location = { lat: null, lon: null };
    }
    this.getGeoLocation();
    if (this.worker.passcode) {
      let alert = this.alertCtrl.create({
        title: this.worker.name,
        inputs: [
          {
            name: 'password',
            placeholder: 'Passcode',
            type: 'password'
          }
        ],
        buttons: [
          {
            text: 'Cancel',
            role: 'cancel',
            handler: data => {
              console.log('Cancel clicked');
            }
          },
          {
            text: 'Sign On',
            handler: data => {
              if ((data.password || '').localeCompare(this.worker.passcode) === 0) {
                this.checkIn();
              } else {
                this.notify("Incorrect passcode");
              }
            }
          }
        ]
      });
      alert.present();
    } else {
      // Logged in as a worker or doesnt have a passcode
      this.checkIn();
    }
  }

  public onSelectItem(item): void {
    item.selected = !item.selected;
    this.allChecked = this.safetyIssues.items.every(i => i.selected) || !this.safetyIssues.items.length;
  }

  private notify(text, duration = 3000) {
    const toast = this.toast.create({
      message: text,
      duration,
      position: 'top'
    });

    toast.present();
  }

  private updateWorkerClockOn() {
    this.worker.clockedOff = false;
    this.worker.clockOff = '';
    this.worker.clockedOn = true;
    this.worker.clockOn = new Date().toTimeString().split(' ')[0];
    this.worker.siteVisits = this.worker.siteVisits || [];
    console.log('Site visits: ', this.worker.siteVisits);
    const visit = new SiteVisit(this.siteId, new Date(), null);
    this.worker.siteVisits.push(visit);
  }

  private checkIn() {
    this.signingOn = true;
    this.updateWorkerClockOn();
    this.checkInService
      .checkIn(this.siteId, this.location.lat, this.location.lon, this.worker.id)
      .subscribe((response) => {
        this.signingOn = false;
        console.log(response);
        if (response.success) {
          this.worker.inLocation = response.in_location;
          // go back to signin page.
          this.navCtrl.popToRoot();
        } else {
          this.notify(response.message);
        }
      }, (error) => {
        this.signingOn = false;
        console.error(error);
        this.notify('Server error on check-in');
        // go back to signin page.
        this.navCtrl.popToRoot();
      });
  }

  private getGeoLocation() {
    return this.geolocation.getCurrentPosition()
      .then((resp) => {
        console.log("Location: lat = " + resp.coords.latitude + " long " + resp.coords.longitude);
        this.location.lat = resp.coords.latitude;
        this.location.lon = resp.coords.longitude;
      })
      .catch((error) => {
        console.log('Error getting location', error);
      });
  }
}



