export interface TimesheetModelInterface {
  id: number;
  user: number;
  site: number;
  clockOn: Date | null;
  clockOff: Date | null;
  // 0 - normal working time
  // 1 - lunch
  state: number;
  // id for supervisor who approved
  approved: number | null;
}

export class TimesheetModel implements TimesheetModelInterface {
  public isApprovable: boolean;
  public extendedHours: boolean;
  public roundedClockOn: Date | null;
  public roundedClockOff: Date | null;

  constructor(
    public id: number,
    public user: number,
    public site: number,
    public clockOn: Date | null,
    public clockOff: Date | null,
    // 0 - normal working time
    // 1 - lunch
    public state: 0 | 1,
    // id for supervisor who approved
    public approved: number | null
  ) { }
}

export class DailyTimesheet {
  constructor(
    public date: Date,
    public dateLabel: string,
    public timesheets: TimesheetModel[]
  ) { }
}



