import { NavParams, ViewController } from "ionic-angular";

import { Component } from "@angular/core";

@Component({
  selector: 'logout-modal',
  template: `
  <ion-header>
    <ion-navbar>
      <ion-title>Logout</ion-title>
    </ion-navbar>
  </ion-header>
  <ion-content>
    <div class="wrapper">
      <ion-item><ion-label>{{ workerName }}</ion-label></ion-item>
      <ion-item>
        <ion-label>Passcode</ion-label>
        <ion-input type="password" [(ngModel)]="passcode" autofocus></ion-input>
      </ion-item>
      <ion-item>
        <ion-label>Lunch</ion-label>
        <ion-checkbox [(ngModel)]="lunch"></ion-checkbox>
      </ion-item>
    </div>
  </ion-content>
  <ion-footer>
    <ion-item class="buttons">
      <button ion-button clear outline (click)="onCancel()">Cancel</button>
      <button ion-button float-right default [disabled]="!passcode" (click)="onSignOff()">Sign off</button>
    </ion-item>
  </ion-footer>
`
})
export class LogoutModal {

  public workerName: string;
  public passcode: string;
  public lunch: boolean;

  constructor(params: NavParams, public viewCtrl: ViewController) {
    this.workerName = params.get('title');
    console.log('Title', this.workerName);
  }

  public onCancel() {
    this.viewCtrl.dismiss();
  }

  public onSignOff() {
    this.viewCtrl.dismiss({ passcode: this.passcode, lunch: this.lunch });
  }

}



