interface SiteSwm {
  swm: number;
  site: number;
}

export class WorkerModel {
  id: number
  name: string;
  passcode: string;
  image: string;
  description: string;
  swms: number[]; //swms for the days
  validSwms: SiteSwm[] = []; // passed certification
  claimedSwms: SiteSwm[] = []; // claimed for certification
  swmCompliant: boolean; // has this worker finished all the swm courses
  tasks: number[]; // tasks allocated for today / this prestart??
  clockOn: string;
  clockedOn: boolean;
  clockOnAddress: string;
  clockOff: string;
  clockOffAddress: string;
  clockedOff: boolean;
  startedWork: boolean;
  finishedWork: boolean;
  date: Date;
  ackSwms: boolean;
  ackIssues: boolean;
  siteSelected: number;
  inLocation: boolean;
  siteVisits: SiteVisit[];
  sitesAllowed: number[];
  supervisor: boolean;

  constructor(data?: any) {
    if (data) {
      Object.assign(this, data);
    }
  }

  public hasValidSwm(site: number, swm: number): boolean {
    return !!this.validSwms.find((i) => i.site === site && i.swm === swm);
  }

  public hasClaimedSwm(site: number, swm: number): boolean {
    return !!this.claimedSwms.find((i) => i.site === site && i.swm === swm);
  }
}

export class WorkersModel {
  items: Array<WorkerModel> = [];
}

export class SiteVisit {
  constructor(
    public id: number,
    public checkInDate: Date | null,
    public checkOutDate: Date | null
  ) { }
}



