import * as moment from 'moment';

import { LoadingController, NavController, NavParams, ToastController } from 'ionic-angular';

import { AuthService } from '../../providers/auth';
import { Component } from '@angular/core';
import { DailyTimesheet } from '../../classes/timesheet.model';
import { Site } from '../../classes/site.model';
import { TimesheetService } from '../../providers/timesheet.service';
import { WorkerService } from '../../providers/worker';

@Component({
  selector: 'page-timesheet-history',
  templateUrl: 'timesheet-history.html',
})
export class TimesheetHistoryPage {
  dailyTimesheets: DailyTimesheet[] = [];
  dailyTimesheetsForSite: DailyTimesheet[] = [];
  noTimesYet: boolean = true;
  lastDate: Date = new Date();
  startDate: string = (new Date()).toISOString();
  loaded = false;
  allowedSites: number[] = [];
  siteSelected: number;

  constructor(
    public workers: WorkerService,
    public navCtrl: NavController,
    public navParams: NavParams,
    private timesheetService: TimesheetService,
    private toast: ToastController,
    private loadingCtrl: LoadingController,
    private auth: AuthService,
  ) { }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TimesheetHistory');
    this.lastDate = new Date();
    this.timesheetService.reset();
    if (this.auth.isSupervisor) {
      if (!this.workers.workers.length) {
        this.workers.getData();
      }
    }
    this.fetchNextPage();
  }

  doInfinite(infiniteScroll) {
    this
      .fetchNextPage()
      .then(() => {
        infiniteScroll.complete();
      })
  }

  public onSiteSelected(site: Site) {
    if (site) {
      this.siteSelected = site.id;
      this.updateDailyTimesheetsForSite();
    }
  }

  public changeStartDate(newDate) {
    this.startDate = newDate;
    const date = new Date(newDate);
    const year = date.getFullYear();
    const month = date.getMonth();
    const day = date.getDate();
    // end of a day
    this.siteSelected = null;
    this.lastDate = new Date(year, month, day, 23, 59, 59);
    this.timesheetService.reset();
    this.fetchNextPage();
  }

  private fetchNextPage(): Promise<void> {
    const loading = this.loadingCtrl.create();
    const nextDate = moment(this.lastDate).subtract(7, 'days').toDate();
    loading.present();
    return this.timesheetService
      .fetch({
        'date__gte': nextDate.toISOString(),
        'date__lte': this.lastDate.toISOString()
      })
      .then((data) => {
        loading.dismiss();
        this.loaded = true;
        this.lastDate = nextDate;
        console.log("got timesheet data in verify times")
        this.dailyTimesheets = data;
        this.updateAllowedSites();
        this.updateDailyTimesheetsForSite();
      }, (error) => {
        loading.dismiss();
        console.error(error);
        this.notify('Server error, try again');
      });
  }

  private notify(text) {
    const toast = this.toast.create({
      message: text,
      duration: 3000,
      position: 'top'
    });
    toast.present();
  }

  private updateAllowedSites() {
    const result: number[] = [];
    this.timesheetService.timesheets.forEach((t) => {
      if (result.indexOf(t.site) < 0) {
        result.push(t.site);
      }
    });
    if (this.siteSelected === undefined) {
      this.siteSelected = result[0];
    }
    this.allowedSites = result;
  }

  private updateDailyTimesheetsForSite() {
    if (this.siteSelected) {
      this.dailyTimesheetsForSite = this.timesheetService.getDailyTimesheetsForSite(this.siteSelected);
    } else {
      this.dailyTimesheetsForSite = [];
    }
    console.log('Daily', this.dailyTimesheetsForSite);
  }
}



