import { AlertController, Tabs } from 'ionic-angular';
import { Component, ViewChild } from '@angular/core';

import { AuthService } from '../../providers/auth';
import { ClockOffPage } from '../clock-off/clock-off';
import { ClockOnPage } from '../clock-on/clock-on';
import { PrestartPage } from '../prestart/prestart';
import { SharedService } from '../../providers/shared';

@Component({
  selector: 'tabs-navigation',
  templateUrl: 'tabs-navigation.html'
})
export class TabsNavigationPage {
  @ViewChild('tabs') public tabRef: Tabs;

  public tab1Root: any;
  public tab2Root: any;
  public tab3Root: any;

  constructor(
    public sharedService: SharedService,
    private alertCtrl: AlertController,
    private auth: AuthService,
  ) {
    //this.tab1Root = ListingPage;
    this.tab1Root = PrestartPage;
    //this.tab2Root = ProfilePage;
    this.tab2Root = ClockOnPage;
    this.tab3Root = ClockOffPage;
  }

  public onPrestartSelect() {
    const prev = this.tabRef.previousTab(false);
    this.checkAndGoPrestart(PrestartPage, prev);
  }

  private checkAndGoPrestart(successRoute, cancelRoute) {
    if (this.sharedService.getSinglePass()) {
      // we have a ticket for single pass to restricted area after sucessful login
      return;
    }
    let alert = this.alertCtrl.create({
      title: 'Please re-enter your password',
      inputs: [
        {
          name: 'password',
          placeholder: 'Password',
          type: 'password'
        }
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: data => {
            if (cancelRoute) {
              this.tabRef.select(cancelRoute);
            }
          }
        },
        {
          text: 'Sign On',
          handler: data => {
            const password = data.password;
            if (!this.auth.checkPassword(password)) {
              if (cancelRoute) {
                this.tabRef.select(cancelRoute);
              }
            }
          }
        }
      ]
    });
    alert.present();
  }

  public siteIsOpen(): boolean {
    return this.sharedService.siteOpened;
  }
}



