import 'rxjs/add/operator/toPromise';

import { ApiService } from './api';
import { Injectable } from '@angular/core';
import { PrestartConfiguration } from '../classes/prestart-configuration.model';

@Injectable()
export class PrestartConfigurationService {
  private _prestarts: PrestartConfiguration[] = [];
  public get prestarts(): PrestartConfiguration[] {
    return this._prestarts;
  }

  constructor(public api: ApiService) {
  }

  public getData(): Promise<PrestartConfiguration[]> {
    console.log("getData prestart");
    const mapPrestart = ({
      site_id,
      tasks,
      swms,
      reminders,
      issues,
      backupStaff
     }) => new PrestartConfiguration(site_id, tasks, swms, reminders, issues, backupStaff);
    return this.api.get('/v/1/0/prestart')
      .toPromise()
      .then(response => response.json().preStarts.map(mapPrestart) || [])
      .then(prestarts => this._prestarts = prestarts)
      .catch(this.handleError);
  }

  public post(configuration: PrestartConfiguration): Promise<void> {
    return this.api
      .post(`/v/1/0/prestart/${configuration.siteId}`, configuration)
      .toPromise()
      .then(() => {
        // save updated record locally
        const existingPrestartIndex = this._prestarts.findIndex(i => i.siteId === configuration.siteId);
        if (existingPrestartIndex >= 0) {
          this._prestarts[existingPrestartIndex] = configuration;
        } else {
          this._prestarts.push(configuration);
        }
      })
      .catch(this.handleError);
  }

  public addBackupStaff(site: number, worker: number): Promise<void> {
    return this.api
      .post(`/v/1/0/site/${site}/backupStaff`, { userId: worker })
      .toPromise()
      .then(() => {
        // save updated record locally
        const existingPrestartIndex = this._prestarts.findIndex(i => i.siteId === site);
        if (existingPrestartIndex >= 0) {
          const backupStaff = this._prestarts[existingPrestartIndex].backupStaff;
          if (backupStaff.indexOf(worker) < 0) {
            backupStaff.push(worker);
          }
        }
      })
      .catch(this.handleError);
  }

  public removeBackupStaff(site: number, worker: number): Promise<void> {
    return this.api
      .delete(`/v/1/0/site/${site}/backupStaff/${worker}`)
      .toPromise()
      .then(() => {
        // save updated record locally
        const existingPrestartIndex = this._prestarts.findIndex(i => i.siteId === site);
        if (existingPrestartIndex >= 0) {
          const backupStaff = this._prestarts[existingPrestartIndex].backupStaff;
          if (backupStaff.indexOf(worker) < 0) {
            backupStaff.push(worker);
          }
        }
      })
      .catch(this.handleError);
  }

  public getBySiteId(siteId: number): PrestartConfiguration {
    return this._prestarts.find(i => i.siteId === siteId);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    this._prestarts = [];
    return Promise.reject(error.message || error);
  }
}



