interface SiteInterface {
  id: number;
  label: string;
  open: boolean;
  openAt: Date | null;
  dayStart: string | null;
  dayEnd: string | null;
}

export class Site implements SiteInterface {
  constructor(
    public id: number,
    public label: string,
    public open: boolean,
    public openAt: Date | null,
    public dayStart: string | null,
    public dayEnd: string | null
  ) { }
}



