import { ApiService } from './api';
import { Injectable } from '@angular/core';
import { TasksModel } from '../classes/task.model';

@Injectable()
export class TaskService {

  constructor(public api: ApiService) {
    console.log('Hello Task Provider');
  }

  getData(): Promise<TasksModel> {
    console.log("getData tasks")
    // original mock in './assets/example_data/task.json'
    return this.api.get('/v/1/0/tasks')
      .toPromise()
      .then(response => response.json())
      // Map server model to client's
      .then(({ tasks }) => (
        {
          items: tasks.map(
            ({ id, name, image, description, sites, created_at, updated_at, swms }) => ({
              id,
              name,
              description,
              sites,
              swms: swms || [],
              image: image || './assets/images/safety-focus/bigstock-Portrait-of-an-industrial-work-41642668.jpg',
            })
          )
        })
      )
      .catch(this.handleError);
  }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }

}



