import { LoadingController, NavController, NavParams } from 'ionic-angular';

import { Component } from '@angular/core';
import { PrestartConfiguration } from '../../classes/prestart-configuration.model';
import { SafetyIssuesPage } from '../safety-issues/safety-issues';
import { SafetyReminderDetailPage } from '../safety-reminder-detail/safety-reminder-detail';
import { SafetyReminderService } from '../../providers/safety-reminder';
import { SafetyRemindersModel } from '../../classes/safety-reminder.model';
import { SharedService } from '../../providers/shared';
import { WorkerModel } from '../../classes/worker.model';

/*
  Safety focus is also known as safety reminder
*/
@Component({
  selector: 'page-safety-reminder',
  templateUrl: 'safety-reminder.html'
})
export class SafetyReminderPage {

  worker: WorkerModel;
  siteId: number;
  prestart: PrestartConfiguration;
  safetyReminders: SafetyRemindersModel = new SafetyRemindersModel();
  allChecked = false;
  userHelperMode: boolean;

  constructor(public navCtrl: NavController, public navParams: NavParams,
    public safetyReminderService: SafetyReminderService,
    public loadingCtrl: LoadingController,
    public sharedService: SharedService, ) {
    this.worker = this.navParams.get('worker');
    this.prestart = this.navParams.get('prestart');
    this.siteId = +this.navParams.get('siteId');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SafetyReminderPage');
    const loading = this.loadingCtrl.create();
    loading.present();
    this.userHelperMode = this.sharedService.userHelperMode;
    const done = () => loading.dismiss();
    this.safetyReminderService
      .getData(this.worker.id)
      .then((sfd) => {
        console.log("got the safety reminders data");
        this.safetyReminders.items = sfd.items;
        if (this.prestart) {
          this.safetyReminders.items = this.safetyReminders.items
            .filter((reminder) => this.prestart.reminders.indexOf(reminder.id) >= 0);
        }

        console.log(this.safetyReminders);

        if (this.safetyReminders.items.length === 0) {
          this.allChecked = true;
        }
      })
      .then(done, done);
  }

  gotoSafetyRemniderDetail(safetyReminder) {
    console.log("gotoSafetyReminderDetail")
    this.navCtrl.push(SafetyReminderDetailPage, { 'worker': this.worker, 'safetyRemnider': safetyReminder });
  }

  public onSelectItem(item): void {
    item.selected = !item.selected;
    this.allChecked = this.safetyReminders.items.every(i => i.selected);
  }

  gotoSafetyIssues() {
    console.log("gotoSafetyIssues")
    this.navCtrl.push(SafetyIssuesPage, {
      'worker': this.worker,
      'siteId': this.siteId,
      'prestart': this.prestart,
    });
  }
}



