import 'rxjs/Rx';

import { AlertController, ModalController, NavController, ToastController } from 'ionic-angular';
import { DEFAULT_PERSON, WorkerService } from '../../providers/worker';

import { CheckInOutService } from '../../providers/check-in';
import { Component } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation';
import { LogoutModal } from '../logout-modal/logout-modal.component';
import { SharedService } from '../../providers/shared';
import { Subscription } from 'rxjs/Subscription';
import { WorkersModel } from '../../classes/worker.model';

const TAB_UPDATE_INTERVAL = 60 * 1000;

@Component({
  selector: 'page-clock-off',
  templateUrl: 'clock-off.html'
})
export class ClockOffPage {
  defaultPersonImage = DEFAULT_PERSON;
  workers: WorkersModel = new WorkersModel();
  workersWorking: WorkersModel = new WorkersModel();
  workersFinished: WorkersModel = new WorkersModel();
  loading: any;
  hasDayStarted: boolean;
  public location: {
    lat: number
    lon: number
  } = {
      lat: null,
      lon: null
    };
  private updateIntervalHandler;
  private pageLeaveSubscription: Subscription;
  private inHandler = false;

  constructor(
    public nav: NavController,
    public workerService: WorkerService,
    public alertCtrl: AlertController,
    private geolocation: Geolocation,
    private sharedService: SharedService,
    private toast: ToastController,
    private checkOutService: CheckInOutService,
    private modalCtrl: ModalController
  ) { }

  ionViewDidEnter() {
    console.log("ionViewDidEnter - clockOff");
    clearInterval(this.updateIntervalHandler);
    this.updateIntervalHandler = setInterval(() => {
      // Interval could be triggered multiple times in row
      if (this.inHandler) {
        return;
      }
      this.inHandler = true;
      this.updateData();
      this.inHandler = false;
    }, TAB_UPDATE_INTERVAL);
    this.pageLeaveSubscription = this.sharedService.onPageLeave.subscribe(() => {
      clearInterval(this.updateIntervalHandler);
    });
    this.updateData();
  }

  ionViewWillLeave() {
    clearInterval(this.updateIntervalHandler);
    this.pageLeaveSubscription.unsubscribe();
    console.log('Clearing ticker');
  }

  updateData() {
    this.workers.items = this.workerService.getWorkersStatus();
    this.createViewArrays();
    if ((this.workersWorking.items.length + this.workersFinished.items.length) === 0) {
      this.hasDayStarted = false;
    } else this.hasDayStarted = true;

    this.getGeoLocation();
  }

  createViewArrays() {
    // parse the workers array to provide swm compilance indication
    // and whether the worker has started work or not.

    this.workersWorking.items = [];
    this.workersFinished.items = [];
    this.workers.items.map((worker) => {
      // do swm compliance
      console.log("print out worker")
      console.log(worker)

      // split workers into started and not started arrays - exclude
      // workers that are finished for the day
      if (worker.clockedOn === true) {
        console.log("clocked on detected ")
        console.log(worker)
        if (!worker.clockedOff) {
          console.log(worker.name + " is still working")
          if (worker.siteSelected === this.sharedService.siteId) this.workersWorking.items.push(worker);
        } else {
          console.log(worker.name + " has finished work")
          if (worker.siteSelected === this.sharedService.siteId) this.workersFinished.items.push(worker);
        }
      }
    });
    console.log("worker clocking")
    console.log(this.workersWorking.items)
  }

  startSignOff(worker) {
    this.getGeoLocation();
    console.log("start the signoff process for " + worker.name);
    let logoutModal = this.modalCtrl.create(LogoutModal, { title: worker.name }, { showBackdrop: true, enableBackdropDismiss: true });
    logoutModal.onDidDismiss(data => {
      if (!data) {
        return;
      }
      if (data.passcode.localeCompare(worker.passcode) === 0) {
        this.clockOff(worker, data.lunch, this.location)
      } else {
        this.notify('Invalid passcode');
      }
    });
    logoutModal.present();
  }

  private clockOff(worker, lunch, location) {
    worker.clockedOff = true;
    worker.clockOff = new Date().toTimeString().split(' ')[0];
    this.checkOutService.checkOut(
      this.sharedService.siteId,
      lunch,
      location.lat,
      location.lon,
      worker.id
    ).subscribe((response) => {
      if (!response.success) {
        this.notify(response.message);
      }
    }, (error) => {
      this.notify('Server error');
      console.error(error);
    });
    this.createViewArrays();
  }

  private notify(text) {
    const toast = this.toast.create({
      message: text,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  private getGeoLocation() {
    this.geolocation.getCurrentPosition().then((resp) => {
      console.log("Location: lat = " + resp.coords.latitude + " long " + resp.coords.longitude);
      this.location.lat = resp.coords.latitude;
      this.location.lon = resp.coords.longitude;
    }).catch((error) => {
      console.log('Error getting location', error);
    });
  }
}



